import { SodiumPlus, X25519PublicKey, X25519SecretKey, Ed25519PublicKey, Ed25519SecretKey, CryptographyKey } from 'sodium-plus';
import { ToastrService } from 'ngx-toastr';
import { WebSocketService } from './../../../shared/services/websocket';
const version = require('../../../../../package.json').version;

let sodium;
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss'],
  animations: [SharedAnimations]
})

export class SignupComponent implements OnInit {
  loading: boolean;
  emailSent: boolean = false;
  emailAddress: string = "";
  validToken: boolean = false;
  loadingText: string;
  signupForm: FormGroup;
  verifyForm: FormGroup;
  currentStep: any = 1;
  recoveryKey: string = "";
  hasSavedRecovery: any;
  public appVersion: string = version;
  firmwarePercent: any = 0;
  jemRK:any;
  jemID:any;
  verificationURL: any;

  constructor(
    private WSS: WebSocketService,
    private fb: FormBuilder,
    private auth: AuthService,
    private router: Router,
    private jemService: JemService,
    private toastr: ToastrService,
    public activatedRoute: ActivatedRoute, 
    public sanitizer: DomSanitizer
  ) {}

  get isMobileAgent() {
    const agent = navigator.userAgent || navigator.vendor || (window as any).opera;
    // tslint:disable-next-line:max-line-length
    return (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(agent) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(agent.substr(0, 4)));
}

  async ngOnInit() {
    this.verifyForm = this.fb.group({emailAddress: new FormControl(null, { validators: [Validators.required, Validators.email], updateOn: 'submit'})})
    this.signupForm = this.fb.group({
      firstName: new FormControl(null, { validators: [Validators.required], updateOn: 'submit'}),
      lastName: new FormControl(null, { validators: [Validators.required], updateOn: 'submit'}),
      jemID: new FormControl(null, { validators: [Validators.required], updateOn: 'submit'})
    })
    if(this.activatedRoute.snapshot.params.token) {
      if(this.isMobileAgent)
      {
        window.location.href = 'jempass://signup/verify/' + this.activatedRoute.snapshot.params.token;
      }
      this.auth.verifyPasetoToken(this.activatedRoute.snapshot.params.token).subscribe(res => {
        this.validToken = true;
        this.currentStep = 3;
        this.emailAddress = res;
    }, (error) => {
        this.validToken = false;
        this.currentStep = 1;
    });
    }
  this.verificationURL = window.location.href; 
  this.verificationURL = this.verificationURL.replace(/^https:\/\//, "jempass://");
  }

  isElectron() {
    if(typeof process !== "undefined" && process.versions.hasOwnProperty('electron')) return true;
    return false;
}

  get email() { return this.verifyForm.get('emailAddress'); }

  async verify() {
    if(this.verifyForm.invalid) return;
    this.loading = true;
    this.loadingText = 'Sending validation email...';
    this.auth.confirmEmailAddress(this.verifyForm.value.emailAddress)
    .subscribe(res => {

      if(res.status == 400) {
        this.toastr.error("User already exists", 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
        this.verifyForm.reset();
        this.loading = false;
        return;
      }

        this.loading = false;
        this.emailSent = true;
        this.currentStep = 2;
    }, (error) => {
      this.toastr.error("Couldn't send verification email. " + error.error.message, 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      this.verifyForm.reset();
      this.loading = false;
    });
  }

  goToDashboard() {
    if(!this.hasSavedRecovery) return;
    this.auth.firstSignIn = true;
    this.WSS.disconnectAPI();
    this.router.navigate(["/signin/", { emailAddress: this.emailAddress, jemID: this.jemID }]);
  }

  private copy(item: string) {
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = item;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    this.toastr.success('Your JEM Recovery Key has been copied to your clipboard. Save it!', 'Success!', { timeOut: 5000 });
  }

xorEncode(txt, pass) {

  var ord = []
  var buf = ""
  
  for (var z = 1; z <= 255; z++) {ord[String.fromCharCode(z)] = z}
  
  for (var j = z = 0; z < txt.length; z++) {
      buf += String.fromCharCode(ord[txt.substr(z, 1)] ^ ord[pass.substr(j, 1)])
      j = (j < pass.length) ? j + 1 : 0
  }
  
  return buf;
  }

async getRandomInt(min, max) {
    sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    var byteArray = await sodium.randombytes_buf(1);
    var range = max - min + 1;
    var max_range = 256;
    if (byteArray[0] >= Math.floor(max_range / range) * range)
        return this.getRandomInt(min, max);
    
    return min + (byteArray[0] % range);
}

async random_str(length, keyspace = 'ABCDEFGHJKLMNPQRSTVWXYZ23456789')
{
    var pieces = [];
    var max = (keyspace.length- 1);
    for (var i = 0; i < length; ++i) {
        pieces.push(keyspace[await this.getRandomInt(0,max)]);
    }
    return pieces.join("");
}

  async signup() {
    sodium = (!sodium) ? await SodiumPlus.auto(): sodium;
    if(this.signupForm.invalid) return;

    // IF THE USER HAS USED UNDERSCORES INSTEAD OF DASHES, CONVERT THEM
    this.signupForm.patchValue({jemID: this.signupForm.value.jemID.replace(/_/g ,"-")})
    
    this.loading = true;
    this.loadingText = 'Looking for user...';

    // CHECK IF THE USER ALREADY EXISTS
    var doesUserExist = await this.auth.doesUserExist(this.emailAddress).toPromise();
    if (doesUserExist.status == 1) {
      this.loading = false;
      this.toastr.error("User already exists", 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      return;
    }

    // CHECK IF THE JEM IS ALREADY REGISTERED
    var isJEMAvailable = await this.auth.isJEMAvailable(this.signupForm.value.jemID).toPromise();
    if (isJEMAvailable.status == 1) {
      this.loading = false;
      this.toastr.error("JEM already registered", 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
      return;
    }

    // GENERATE DEVICE KEYS
    this.loadingText = 'Generating device keys...';
    var deviceKeys = await this.auth.generateMQTTKeys({"email": this.emailAddress});
  
    // CONNECT TO API
    await this.WSS.connectToAPI(deviceKeys.username, this.emailAddress);

    // GENERATE RECOVERY KEYS
    this.loadingText = 'Generating recovery keys...';
    var recoveryKey = await sodium.randombytes_buf(32);
    this.jemRK = "JEM-RK-" + await this.random_str(26);
    var jemRK_hash = await sodium.crypto_generichash(this.jemRK, '', 32);
    var recoveryKeyXOR = this.xorEncode(recoveryKey.toString('hex'), jemRK_hash.toString('hex'));

    // MOVE TO STEP 4 - TURN YOUR JEM ON
    this.currentStep = 4;

    // ADD A SERVICE EMITTER LISTENER FOR JEMSTATUS
    this.jemService.serviceEmitter.on("JEMSTATUS", (status) => {
      switch(status) {
        case "FIRMWARE_UPGRADE":
          // START A TIMEOUT TO CHECK IF FIRMWARE HAS STARTED
          setTimeout(() => {
            if(this.firmwarePercent === 0) {
              this.toastr.error("It appears our firmware update hasn't started.  Has your JEM gone offline? Was it fully charged before you started? Is your jemPass application still running?", 'Error', { timeOut: 25000, closeButton: true, progressBar: true  });
              this.currentStep = 3;
              this.verifyForm.reset();
              this.loading = false;
            }
          }, 20000)

          this.currentStep = 5; // firmware upgrade screen
          this.jemService.serviceEmitter.on("JEM_FIRMWARE", (jem) => {
            this.jemService.jemFound = true;
            this.firmwarePercent = jem.percent;
            if(this.firmwarePercent == 100) {
              this.jemService.firmwareUpgraded = true;
              setTimeout(() => {
                this.currentStep = 6;
              }, 1000)
            }
          });
        break;

        case "PROVISION_NO_FINGER":
          this.currentStep = 7; // fingerprint enrollment screen
        break;

        case "PROVISION_FINGER":
          this.currentStep = 8;
          this.jemID = this.signupForm.value.jemID;
        break;

        case "offline":
          // JEM has gone offline.  Set its ID to null, so "signin" knows to follow normal procedure. Otherwise, the JEM's ID prompts auto signin.
          this.jemID = "";
        break;

        default:
      }
    });

    // PROVISION FIRST JEM
    await this.jemService.provisionFirstJEM(this.signupForm.value.jemID, deviceKeys.username, this.emailAddress, deviceKeys, recoveryKey)
    .then(async data => {

      // CREATE USER
      this.loadingText = 'Creating user...';
      const user = await this.auth.createUser(
        {
          'token': this.activatedRoute.snapshot.params.token,
          'firstName': this.signupForm.value.firstName,
          'lastName': this.signupForm.value.lastName,
          "jemID": this.signupForm.value.jemID,
          "device": data.provisionResponse,
          "userEncryptionPublicKey": data.provisionResponse.userEncPubKey.toString('base64'),
          "userSigningPublicKey": data.provisionResponse.userSigPubKey.toString('base64'),
          "userEncryptedVEK": data.provisionResponse.vekKeyEnc.toString('base64'),
          "JEMdeviceEncryptionPublicKey": data.provisionResponse.devEncPubKey.toString('base64'),
          "JEMdeviceSigningPublicKey": data.provisionResponse.devSigPubKey.toString('base64'),
          "userEncryptionPrivateKey": data.provisionResponse.userEncPrivKeyEnc.toString('base64'),
          "userSigningPrivateKey": data.provisionResponse.userSigPrivKeyEnc.toString('base64'),
          "recoveryKeyXOR": btoa(recoveryKeyXOR),
          "encryptionPrivateKeyKEK": data.encryptionPrivateKeyKEK.toString('base64'),
          "signingPrivateKeyKEK": data.signingPrivateKeyKEK.toString('base64')
        }
      )
      .subscribe(async(res) => {
        // THE USER HAS BEEN CREATED SUCCESSFULLY - ACK THE PROVISION WITH THE JEM
        this.jemService.acknowledgeProvision(this.signupForm.value.jemID, X25519PublicKey.from(data.provisionResponse.devEncPubKey, 'base64'), X25519PublicKey.from(deviceKeys.encryptPublicKey, 'base64'), X25519SecretKey.from(deviceKeys.encryptPrivateKey, 'base64'), "device/" + deviceKeys.username + "/response");
        this.currentStep = 9;
      }, (error) => {
          this.toastr.error("Couldn't create user. " + error.error.message, 'Error', { timeOut: 5000, closeButton: true, progressBar: true  });
          this.verifyForm.reset();
          this.loading = false;
        });
    }, (error) => {
          this.WSS.disconnectAPI();
          this.toastr.error(error, 'Error', { timeOut: 10000, closeButton: true, progressBar: true  });
          this.currentStep = 3;
          this.loading = false;
    }); 
  }
}