import { Component, OnInit } from '@angular/core';
import { SharedAnimations } from 'src/app/shared/animations/shared-animations';
import { Router, ActivatedRoute } from '@angular/router';
const version = require('../../../../../package.json').version;
import { SodiumPlus } from 'sodium-plus';

@Component({
    selector: 'app-signin',
    templateUrl: './signinWithJEMNetwork.component.html',
    styleUrls: ['./signin.component.scss'],
    animations: [SharedAnimations]
})
export class SigninComponent {
    currentStep = 1;
    public appVersion: string = version;

    // These constants should probably be set in environment.ts ... 
    CLIENT_ID: string = '1234'; 
    REDIRECT_URI: string = 'https://my-staging.jempass.com/dashboard/v2'; 
    SCOPE: string = 'jemDecrypt jemEncrypt jemSign userProfile userKeys_read userJems_read'; 

    // Assign a random nonce to STATE at each use in the signInWithJEMNetwork() function 
    STATE: string = ''; 

    constructor(
        private route: ActivatedRoute,
        private router: Router,
    ) {}

    async getRandomInt(min, max) {
		var sodium = !sodium ? await SodiumPlus.auto() : sodium;
		var byteArray = await sodium.randombytes_buf(1);
		var range = max - min + 1;
		var max_range = 256;
		if (byteArray[0] >= Math.floor(max_range / range) * range) return this.getRandomInt(min, max);

		return min + (byteArray[0] % range);
	}

    async random_str() {

        const CHARACTER_SETS = [
            ['Numbers', '0123456789'],
            ['Lowercase', 'abcdefghijklmnopqrstuvwxyz'],
            ['Uppercase', 'ABCDEFGHIJKLMNOPQRSTUVWXYZ']
        ];

		var pieces = [];
		var length = 32;
		var keyspace = '';
		keyspace = CHARACTER_SETS[0][1]; 
		keyspace += CHARACTER_SETS[1][1]; 
		keyspace += CHARACTER_SETS[2][1]; 
		var max = keyspace.length - 1;
		for (var i = 0; i < length; ++i) {
			pieces.push(keyspace[await this.getRandomInt(0, max)]);
		}
		return pieces.join('');
	}

    async signInWithJEMNetwork() {

        this.STATE = await this.random_str(); 
        
        // Construct the signInUrl 
        const jemNetworkSignInUrl = 
            'https://jem.network/sign-in/ouath2?response_type=code'
            + '&client_id=' + encodeURIComponent(this.CLIENT_ID)
            + '&redirect_uri=' + encodeURIComponent(this.REDIRECT_URI)
            + '&scope=' + encodeURIComponent(this.SCOPE)
            + '&state=' + encodeURIComponent(this.STATE); 
            
        // Redirect window to the constructed url 
        console.log(jemNetworkSignInUrl);
        // window.location.href = jemNetworkSignInUrl;
    }

}